import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { governothers } from '../../menus';

/**
 * Generated class for the MobilemoneyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-government2',
  templateUrl: 'government2.html',
})
export class GovernmentPage2 {

  langs:any = {};
  govaList:any;
  
  utilities: any = [];
  constructor( public alertService: AlertServiceProvider,public navCtrl: NavController,public globalVars: GlobalVarsProvider,
     public navParams: NavParams) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() ); 
    this.utilities = governothers;
  
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad GovernmentPage2');
  }
  goHome(){
    this.navCtrl.setRoot('HomePage');
  }

  openpage( dat ) {
    
    console.log("*****************",dat);
    this.navCtrl.push( 'CollectionPage', { data: dat } );
  }

}



